const useRefresh = () => {
  const { fast, slow } = useContext(RefreshContext)
  return { fastRefresh: fast, slowRefresh: slow }
}

const loadWalletNFTs = (dataWallet) => {
  if (dataWallet === undefined) return [];

  if (dataWallet.length === 0) return [];

  const ret = dataWallet.map((item) => {
    let index = -1;

    for (let i = 0; i < GOTCHA_NFTS.length; i++) {
      if (
        GOTCHA_NFTS[i].address.toLowerCase() === item.token_address.toLowerCase()
      ) {
        index = i;
        break;
      }
    }

    if (index === -1) return null;

    const nftInfo = GOTCHA_NFTS[index];
    let imageURL = "";
    if (nftInfo.name.includes("LIFE OF HEL - Volume"))
      imageURL = `${nftInfo.imageHash}${VOLUME_METADATA[Number(item.token_id.toString()) - 1]
        }.${nftInfo.imageType}`;
    else if (nftInfo.name.includes("LOADED LIONS"))
      imageURL = `${nftInfo.imageHash}${'Lion.jpg'}`;
    else
      imageURL = `${nftInfo.imageHash}${Number(item.token_id.toString())}.${nftInfo.imageType
        }`;

    return {
      id: item.token_id.toString(),
      name: item.name,
      address: nftInfo.address,
      image: imageURL
    };
  });

  const finalArray = ret.filter((item) => item !== null);
  return finalArray;
};

const loadStakedNFTs = (dataContract) => {
  console.log("dataContract = ", dataContract)
  if (dataContract === undefined) return [];

  if (dataContract.length === 0) return [];

  const ret = dataContract.map((item) => {
    let index = -1;

    for (let i = 0; i < GOTCHA_NFTS.length; i++) {
      if (GOTCHA_NFTS[i].address.toLowerCase() === item.token_address.toLowerCase()) {
        index = i;
        break;
      }
    }

    if (index === -1) return null;

    const nftInfo = GOTCHA_NFTS[index];
    const tokenId = parseInt(item.token_id.toString());

    let imageURL = "";
    if (nftInfo.name.includes("LIFE OF HEL - Volume"))
      imageURL = `${nftInfo.imageHash}${VOLUME_METADATA[Number(item.token_id.toString()) - 1]
        }.${nftInfo.imageType}`;
    else if (nftInfo.name.includes("LOADED LIONS"))
      imageURL = `${nftInfo.imageHash}${'Lion.jpg'}`;
    else
      imageURL = `${nftInfo.imageHash}${Number(item.token_id.toString())}.${nftInfo.imageType
        }`;

    return {
      name: nftInfo.name,
      image: imageURL,
      address: nftInfo.address,
      id: tokenId
    };
  });

  const finalArray = ret.filter((item) => item !== null);
  return finalArray;
};

let walletNFTSelection = [];
let stakedNFTSelection = [];

const Earn = () => {
  const tokenInfo = [
    { h: 'HOW TO EARN $GOTCHA', d: 'STAKE ELIGIBLE NFTS' },
    { h: 'TOKEN STAKING POOL (%)', d: '$1BN (10%)' },
    { h: 'DAILY DISTRIBUTION TO THE NFT POOL', d: '2,739,726 $ GOTCHA DAILY' },
    { h: 'STAKING OPENING', d: 'ONE WEEK FROM THE PRE-SALE DATE' }
  ];

  return (
    <table className="earn-table">
      <tbody>
        {
          tokenInfo.map(info => (
            <tr>
              <th>{info.h}</th>
              <td>{info.d}</td>
            </tr>
          ))
        }
      </tbody>
    </table>
  )
}

const NFTStakingTable = ({ isMobile }) => {
  const tableData = [
    {
      label: <div className="d-flex justify-content-center align-items-center gap-2 flex-column">
        <Image src={LOHLogo} width="70px" />
        VOLUMES
      </div>,
      type: "first-edition",
      images: [
        Normal1smImg,
        Normal2smImg,
        Normal3smImg,
        Normal4smImg,
        Normal5smImg,
        Normal6smImg,
      ],
      weight: '8',
    },
    {
      label: <div className="d-flex justify-content-center align-items-center gap-2 flex-column">
        <Image src={LOHLogo} width="70px" />
        REAPERS
      </div>,
      type: "avatar",
      images: [
        Avatar0smImg,
        Avatar00smImg,
        Avatar1smImg,
        Avatar2smImg,
        Avatar3smImg,
        Avatar4smImg
      ],
      weight: '27',
    },
    {
      label: <div className="d-flex justify-content-center align-items-center gap-2 flex-column">
        <Image src={LoadedLionsLogo} width="90px" />
        Loaded Lions
      </div>,
      type: "jade",
      images: [
        Lion1Img,
        Lion2Img,
        Lion3Img,
        Lion4Img,
        Lion5Img,
        Lion6Img,
      ],
      weight: '43',
    }
  ];

  return (
    <>
      {
        isMobile ? (
          tableData.map((datum) => (
            <EarnedTicketCard
              key={datum.type}
              data={datum}
            />
          ))
        ) :
          <table className="earned-tickets-table">
            <thead>
              <tr>
                <th rowSpan={2}></th>
                <th rowSpan={2} className="underline image-cell">
                  nft type
                </th>
                <th colSpan={4} className="">
                  NFT WEIGHT
                </th>
              </tr>
              {/* <tr>
                <th>7 - day</th>
                <th>30 - day</th>
                <th>60 - day</th>
                <th>90 - day</th>
              </tr> */}
            </thead>
            <tbody>
              {tableData.map((row) => {
                return (
                  <tr key={row.type}>
                    <th className="nft-type text-right">{row.label}</th>
                    <td className="image-cell">
                      {row.images.map((image, idx) => {
                        return (
                          <img
                            src={image}
                            alt={`image-${row.type}${idx}.jpg`}
                            key={idx}
                            className="nft-image"
                          />
                        );
                      })}
                    </td>
                    <td>
                      {row.weight}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
      }
    </>
  )
}

export default function EarnPage() {
  const isMobile = useMediaQuery("only screen and (max-width: 768px)");

  const { openConnectModal } = useConnectModal();

  // const { callContractWait } = useContext(Web3Context);
  const chainId = useChainId();
  const account = useAccount();

  const { fastRefresh } = useRefresh();

  const { data: txResult, writeContractAsync } = useWriteContract();

  const nftAddresses = GOTCHA_NFTS.map((item) => item.address);

  const { data: timeToStart, refetch: refetchTimeToStart } = useReadContract({
    address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
    abi: StakingAbi,
    functionName: "timeToStart",
    args: [],
    chainId: chainId,
  });

  const { data: dataWallet } = useSWR(
    [account.address ? account.address : ethers.ZeroAddress],
    { fetcher: moralisFetcher(account.address ? account.address : ethers.ZeroAddress), }
  );

  const { data: dataContract, refetch: refetchDataContract } = useReadContract({
    address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
    abi: StakingAbi,
    functionName: "getStakingInfo",
    args: [nftAddresses, account.address ? account.address : ethers.ZeroAddress],
    chainId: chainId,
  });

  const { data: collectionApprovals, refetch: refetchCollectionApprovals } = useReadContract({
    address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
    abi: StakingAbi,
    functionName: "getApprovalStatus",
    args: [nftAddresses, account.address ? account.address : ethers.ZeroAddress],
    chainId: chainId,
  });

  const { data: totalDistributed, refetch: refetchTotalDistributed } = useReadContract({
    address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
    abi: StakingAbi,
    functionName: "totalDistributed",
    args: [],
    chainId: chainId,
  });

  const { data: claimedReward, refetch: refetchClaimedReward } = useReadContract({
    address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
    abi: StakingAbi,
    functionName: "claimedReward",
    args: [account.address ? account.address : ethers.ZeroAddress],
    chainId: chainId,
  });

  const { data: pendingReward, refetch: refetchPendingReward } = useReadContract({
    address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
    abi: StakingAbi,
    functionName: "pendingReward",
    args: [account.address ? account.address : ethers.ZeroAddress],
    chainId: chainId,
  });

  const [isStakingOpened, setIsStakingOpened] = useState(false)
  const regInputForCollectionId = useRef()
  const [selectedCollection, setSelectedCollection] = useState(COLLECTION_OPTIONS[0]);
  const [selectStatusInWallet, setSelectStatusInWallet] = useState([]);
  const [selectStatusStaked, setSelectStatusStaked] = useState([]);
  const [disabledClaimBtn, setDisabledClaimBtn] = useState(true);
  const [disabledUnStakeBtn, setDisabledUnStakeBtn] = useState(true);
  const [needApproval, setNeedApproval] = useState(true);
  const [isApproving, setIsApproving] = useState(false);
  const [isStaking, setIsStaking] = useState(false);
  const [isUnstaking, setIsUnstaking] = useState(false);
  const [isClaiming, setIsClaiming] = useState(false);
  const [disabledStakeBtn, setDisabledStakeBtn] = useState(true);

  const filteredDataWallet = dataWallet ? dataWallet.filter((item) => item.token_address.toLowerCase() === selectedCollection.address.toLowerCase()) : [];
  let nftsInWallet = loadWalletNFTs(filteredDataWallet);

  let filteredDataContract = dataContract && dataContract.length > 0 ? dataContract.filter((item) => item.token_address.toLowerCase() === selectedCollection.address.toLowerCase()) : [];
  const nftsStaked = loadStakedNFTs(filteredDataContract);


  const handleSelectNFTInWallet = (index) => {
    const itemInfo = nftsInWallet[index];

    const selectedItem = walletNFTSelection.find(
      (item) =>
        item.id === itemInfo.id &&
        item.address === itemInfo.address.toLowerCase()
    );
    if (selectedItem === undefined)
      walletNFTSelection.push({
        id: itemInfo.id,
        address: itemInfo.address.toLowerCase(),
      });
    else
      walletNFTSelection = walletNFTSelection.filter(
        (item) => item !== selectedItem
      );

    let items = [];
    for (let i = 0; i < nftsInWallet.length; i++) {
      const itemInfo = nftsInWallet[i];
      const currentItem = walletNFTSelection.find(
        (item) =>
          item.id === itemInfo.id &&
          item.address === itemInfo.address.toLowerCase()
      );
      if (currentItem === undefined) items.push(false);
      else items.push(true);
    }
    setSelectStatusInWallet(items);

    if (walletNFTSelection.length === 0 && !needApproval)
      setDisabledStakeBtn(true);
    else
      setDisabledStakeBtn(false);
  };

  const handleSelectNFTStaked = (index) => {
    const itemInfo = nftsStaked[index];

    const selectedItem = stakedNFTSelection.find(
      (item) =>
        item.id === itemInfo.id &&
        item.address === itemInfo.address.toLowerCase()
    );
    if (selectedItem === undefined)
      stakedNFTSelection.push({
        id: itemInfo.id,
        address: itemInfo.address.toLowerCase(),
      });
    else
      stakedNFTSelection = stakedNFTSelection.filter(
        (item) => item !== selectedItem
      );

    let items = [];
    for (let i = 0; i < nftsStaked.length; i++) {
      const itemInfo = nftsStaked[i];
      const currentItem = stakedNFTSelection.find(
        (item) =>
          item.id === itemInfo.id &&
          item.address === itemInfo.address.toLowerCase()
      );
      if (currentItem === undefined) items.push(false);
      else items.push(true);
    }
    setSelectStatusStaked(items);

    if (stakedNFTSelection.length === 0) {
      setDisabledUnStakeBtn(true);
    } else {
      setDisabledUnStakeBtn(false);
    }
  };

  const handleSelectAllInWallet = () => {
    let items = [];
    walletNFTSelection = [];
    for (let i = 0; i < nftsInWallet.length; i++) {
      const itemInfo = nftsInWallet[i];
      walletNFTSelection.push({
        id: itemInfo.id,
        address: itemInfo.address.toLowerCase(),
      });
      items.push(true);
    }
    setSelectStatusInWallet(items);
    setDisabledStakeBtn(false);
  };

  const handleUnselectAllInWallet = () => {
    let items = [];
    walletNFTSelection = [];
    setSelectStatusInWallet(items);
    setDisabledStakeBtn(true);
  };

  const handleSelectAllStaked = () => {
    let items = [];
    stakedNFTSelection = [];
    for (let i = 0; i < nftsStaked.length; i++) {
      const itemInfo = nftsStaked[i];
      stakedNFTSelection.push({
        id: itemInfo.id,
        address: itemInfo.address.toLowerCase(),
      });
      items.push(true);
    }
    setSelectStatusStaked(items);
    setDisabledUnStakeBtn(false);
  };

  const handleUnselectAllStaked = () => {
    let items = [];
    stakedNFTSelection = [];
    setSelectStatusStaked(items);

    setDisabledUnStakeBtn(true);
  };

  const handleStake = async () => {
    if (needApproval) {
      setIsApproving(true);

      await writeContractAsync({
        address: chainId === TEST_NET ? selectedCollection.address : selectedCollection.address,
        abi: ERC721AAbi,
        functionName: "setApprovalForAll",
        args: [chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN, true],
      })
        .then(() => {
          setIsApproving(false);
          refetchCollectionApprovals();
          refetchTimeToStart();
          toast.success("Approve Submitted!");
        })
        .catch((err) => {
          setIsApproving(false);
        });
      return;
    }

    setIsStaking(true);

    const nftToStake = walletNFTSelection.map((item) => {
      return item.address;
    });

    const tokenId = walletNFTSelection.map((item) => {
      return item.id;
    });

    await writeContractAsync({
      address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
      abi: StakingAbi,
      functionName: "stake",
      args: [nftToStake, tokenId],
    })
      .then(() => {
        setIsStaking(false);
        toast.success("Stake Submitted!");
      })
      .catch((err) => {
        // toast.success(err);
        setIsStaking(false);
      });

    handleUnselectAllInWallet();
  };

  const getTextStake = () => {
    if (isApproving) {
      return "Approving ...";
    }
    if (needApproval) {
      return "Approve";
    }

    if (isStaking) {
      return "Staking...";
    }

    return "Stake";
  };

  const handleUnStake = async () => {
    const nftToUnStake = stakedNFTSelection.map((item) => {
      return item.address;
    });

    const tokenId = stakedNFTSelection.map((item) => {
      return item.id;
    });

    setIsUnstaking(true);

    await writeContractAsync({
      address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
      abi: StakingAbi,
      functionName: "unStake",
      args: [nftToUnStake, tokenId],
    })
      .then(() => {
        setIsUnstaking(false);
        refetchDataContract();
        toast.success("Unstake Submitted!");
      })
      .catch((err) => {
        setIsUnstaking(false);
      });

    handleUnselectAllStaked();
  };

  const getTextUnstake = () => {
    if (isUnstaking) {
      return "Unstaking...";
    }

    return "Unstake";
  };

  const handleClaim = async () => {
    setIsClaiming(true);

    await writeContractAsync({
      address: chainId === TEST_NET ? STAKING_ADDRESS_TEST : STAKING_ADDRESS_MAIN,
      abi: StakingAbi,
      functionName: "claimRewards",
      args: [],
    })
      .then(() => {
        setIsClaiming(false);
        refetchTotalDistributed();
        refetchClaimedReward();
        refetchPendingReward();
        toast.success("Claim Submitted!");
      })
      .catch((err) => {
        setIsClaiming(false);
      });
  };

  const getTextClaim = () => {
    if (isClaiming) {
      return "Claiming ...";
    }

    return "Claim";
  };

  useEffect(() => {
    const timestamp = parseInt((Date.now() / 1000).toFixed(0));
    if (timestamp > Number(timeToStart)) {
      setIsStakingOpened(true);
    } else {
      setIsStakingOpened(false);
    }
  }, [timeToStart]);

  useEffect(() => {
    handleUnselectAllInWallet();
    handleUnselectAllStaked();
  }, [selectedCollection]);

  useEffect(() => {
    if (collectionApprovals) {
      const filteredCollectionApprovals = collectionApprovals ? collectionApprovals.filter((item) => item.token_address.toLowerCase() === selectedCollection.address.toLowerCase()) : [];
      setNeedApproval(!filteredCollectionApprovals[0].isApproval);
    }
  }, [collectionApprovals, selectedCollection]);

  useEffect(() => {
    const amount = parseInt(ethers.formatUnits(pendingReward ? pendingReward : 0, "ether"));
    if (amount > 0)
      setDisabledClaimBtn(false);
    else
      setDisabledClaimBtn(true);
  }, [pendingReward])

  useEffect(() => {
    refetchTimeToStart();
    refetchDataContract();
    refetchCollectionApprovals();
    refetchTotalDistributed();
    refetchClaimedReward();
    refetchPendingReward();
  }, [fastRefresh])

  return (
    <Layout>
      {
        account.isConnected ? !isStakingOpened ?
          <>
            <Container fluid>
              <div className='hero'>
                <Image src={Coin1Img} className='coin1' />
                <h1>earn <span>gotcha</span></h1>
                <div className='staking-closed-container'>
                  <div>
                    <p className="staking-closed">Staking is not allowed yet.</p>
                    {/* <p className="staking-closed">Staking is closed. come back on:</p>
                    <p className="time">DD/MM/YY HH:MM</p> */}
                  </div>
                  <Image src={BowGirlGif} id="bow-girl" />
                </div>
              </div>
            </Container>
          </>
          :
          <>
            <Container fluid>
              <div className='hero'>
                <Image src={Coin1Img} className='coin1' />
                <h1>earn <span>gotcha</span></h1>
              </div>
              <div className='staking-open-container'>
                <div className="overview">
                  <div className="table-wrapper">
                    <table>
                      {/* <tr>
                        <td className="text-right">
                          Total NFTs Staked:
                        </td>
                        <td className="text-left text-danger">{nftBalance?.toString()}</td>
                      </tr> */}
                      <tr>
                        <td className="text-right">
                          Total Distributed $GOTCHA:
                        </td>
                        <td className="text-left text-danger">{parseInt(ethers.formatUnits(totalDistributed ? totalDistributed : 0, "ether"))}</td>
                      </tr>
                      <tr>
                        <td className="text-right">
                          Your Claimed Amount:
                        </td>
                        <td className="text-left text-danger">{parseInt(ethers.formatUnits(claimedReward ? claimedReward : 0, "ether"))}</td>
                      </tr>
                      <tr>
                        <td className="text-right">
                          Your Claimable Amount:
                        </td>
                        <td className="text-left text-danger">{parseInt(ethers.formatUnits(pendingReward ? pendingReward : 0, "ether"))}</td>
                      </tr>
                    </table>
                  </div>
                  <RoundButton
                    key="claim"
                    children={getTextClaim()}
                    variant="transparent"
                    disabled={disabledClaimBtn}
                    onClick={handleClaim}
                    classes="mb-5"
                  />
                </div>
                <Combobox ref={regInputForCollectionId} options={COLLECTION_OPTIONS} value={selectedCollection} onChange={(selectedOption) => setSelectedCollection(selectedOption)} />

                <Tabs className="gotcha-staking-tabs">
                  <TabList>
                    <Tab>
                      {/* <h6>Volumes</h6> */}
                      <p>
                        <span>{nftsInWallet ? nftsInWallet.length : 0}</span> in wallet
                      </p>
                    </Tab>
                    <Tab>
                      {/* <h6>Avatars</h6> */}
                      <p>
                        <span>{nftsStaked ? nftsStaked.length : 0}</span> staked
                      </p>
                    </Tab>
                  </TabList>

                  <TabPanel>
                    {nftsInWallet && nftsInWallet.length ? (
                      <>
                        <div className="cards-wrapper mb-5">
                          {nftsInWallet.map((item, index) => (
                            <StakingCard
                              nft={item}
                              key={index}
                              index={index}
                              selectStatus={selectStatusInWallet}
                              handleSelectNFT={handleSelectNFTInWallet}
                            />
                          ))}
                        </div>
                        <div className="btn-group">
                          <button
                            className="select-all-btn"
                            onClick={handleSelectAllInWallet}
                          >
                            select all
                          </button>
                          <RoundButton
                            key="stake"
                            children={getTextStake()}
                            variant="primary"
                            disabled={disabledStakeBtn}
                            onClick={handleStake}
                          />
                        </div>
                      </>
                    ) : (
                      <NoData text='No NFTs in wallet' />
                    )}
                  </TabPanel>
                  <TabPanel>
                    {nftsStaked && nftsStaked.length ? (
                      <>
                        <div className="cards-wrapper mb-5">
                          {nftsStaked.map((item, index) => (
                            <StakingCard
                              nft={item}
                              key={index}
                              index={index}
                              selectStatus={selectStatusStaked}
                              handleSelectNFT={handleSelectNFTStaked}
                            />
                          ))}
                        </div>
                        <div className="btn-group">
                          <button
                            className="select-all-btn"
                            onClick={handleSelectAllStaked}
                          >
                            select all
                          </button>
                          <RoundButton
                            children={getTextUnstake()}
                            key="unstake"
                            variant="transparent"
                            disabled={disabledUnStakeBtn}
                            onClick={handleUnStake}
                          />
                        </div>
                      </>
                    ) : (
                      <NoData text="No staked NFTs" />
                    )}
                  </TabPanel>
                </Tabs>
              </div>
            </Container>
          </>
          :
          <>
            <Container fluid>
              <div className='hero'>
                <Image src={Coin1Img} className='coin1' />
                <Image src={Coin2Img} className='coin2' />
                <h1>earn <span>gotcha</span></h1>
                <p className="note">CONNECT YOUR WALLET AND START STAKING YOUR ELIGIBLE NFTS TO EARN <span>$GOTCHA</span>.<br /><br /> FOR MORE INFORMATION, SCROLL DOWN</p>
                <div className='d-flex justify-content-center'>
                  <ConnectWalletButton onClick={openConnectModal} />
                </div>
              </div>
            </Container>
            <div className="explanation">
              <Container fluid>
                <div className={`d-flex justify-content-between align-items-center ${isMobile ? "flex-column" : ""}`}>
                  <Image src={BigTeacherImg} id="bigteacher" />
                  <div className='content'>
                    <p>Once staked, eligible NFTs will yield $GOTCHA. Earned $GOTCHA change based on NFT type and total size of NFT pool.</p>
                    <div className="d-flex justify-content-center align-items-center gap-1">
                      <p className="nft-staking"><span>NFT</span> staking</p>
                      <Image src={BlackArrowImg} id="arrow" />
                      <Image src={CoinStackImg} id="coinstack" />
                    </div>
                    <p>$GOTCHA will be awarded at the end of each day. Earned $GOTCHA can be claimed at any time by pressing the CLAIM button after connecting your wallet.</p>
                    <p>More details on eligible NFT collections and NFT weights can be found below.</p>
                  </div>
                </div>
              </Container>
              <Image src={Coin3Img} id="coin3" />
              <Image src={HeartImg} id="heart" />
            </div>
            <Container className='earn-table-cointainer'>
              <Earn />
              <Image src={Coin4Img} id="coin4" />
              <Image src={Coin5Img} id="coin5" />
            </Container>
            <div className='nft-staking'>
              <Container fluid>
                <h2>NFT <span>staking</span></h2>
                <p className='note'>How much does my NFT weight in the pool?</p>
                <NFTStakingTable isMobile={isMobile} />
              </Container>
            </div>
          </>
      }
    </Layout>
  )
}
